import React, { useState, useEffect } from 'react';
import { useQuery } from '@apollo/react-hooks';
import { gql } from 'apollo-boost';
import _ from 'lodash';

const getCountries = gql`
  {
    countries {
      code
      name
      phone
      capital
      emoji
      continent {
        name
      }
      languages {
        name
      }
    }
  }
`;

function SearchBar() {
  const { loading, error, data } = useQuery(getCountries);
  const [query, setQuery] = useState('');
  const [group, setGroup] = useState({});
  const [active, setActive] = useState('continent');

  useEffect(() => {}, []);
  const groupBy = (e) => {
    if (e.target.value === 'Language') {
      const byLanguages = _.groupBy(data.countries, 'languages[0].name');
      setGroup(byLanguages);
      setActive('language');
    } else if (e.target.value === 'Continent') {
      const byContinent = _.groupBy(data.countries, 'continent.name');
      setGroup(byContinent);
      setActive('continent');
    }
  };

  const inputHandler = (event) => {
    if (_.isEmpty(group) === true) {
      const byContinent = _.groupBy(data.countries, 'continent.name');
      setGroup(byContinent);
      setActive('continent');
    }
    setQuery(event.target.value);
  };

  return (
    <div className="app">
      <h1>Country search</h1>
      <p>Find information about a country:</p>
      <input
        className="search-bar"
        placeholder="🔍 Type in a country name"
        onChange={inputHandler}
      />
      <div className="group-by">
        <h2>Group by:</h2>
        <button
          className={active === 'continent' ? 'group-btn active' : 'group-btn'}
          onClick={groupBy}
          value="Continent"
        >
          Continent
        </button>
        <button
          className={active === 'language' ? 'group-btn active' : 'group-btn'}
          onClick={groupBy}
          value="Language"
        >
          Language
        </button>
      </div>

      <List query={query} group={group} error={error} loading={loading} />
    </div>
  );
}

function List(props) {
  return (
    <ul className="country-list">
      {Object.entries(props.group).map(([key, value], index) => {
        let countries = value.filter((country) => {
          if (props.query === '') {
            return;
          } else if (
            country.name.toLowerCase().includes(props.query.toLowerCase())
          ) {
            return country;
          }
        });
        if (_.isEmpty(countries) === true) {
          return;
        } else {
          return (
            <div key={index}>
              <h2>{key}</h2>
              <Card
                country={countries}
                query={props.query}
                index={index}
                error={props.error}
                loading={props.loading}
              />
            </div>
          );
        }
      })}
    </ul>
  );
}

function Card(props) {
  if (props.loading) return 'Loading...';
  if (props.error) return `Error! ${props.error.message}`;

  return (
    <div>
      {props.country.map((result, index) => (
        <div className="card" key={index}>
          <li>
            <span>{result.emoji}</span>
            <h2 className="country-name"> {result.name}</h2>
            <h3>
              Capital City:
              <p className="p"> {result.capital}</p>
            </h3>
            <h3>
              Country Code:
              <p className="p"> +{result.phone}</p>
            </h3>
          </li>
        </div>
      ))}
    </div>
  );
}
export { SearchBar };
